// @ts-ignore
import segmentTexture from "./textures/segment.png"
import {
    BoxGeometry,
    DoubleSide,
    Group,
    Mesh,
    MeshLambertMaterial,
    MeshStandardMaterial,
    Texture,
    TextureLoader,
} from "three"
import { TextGeometry } from "three/examples/jsm/geometries/TextGeometry"
import { FontLoader } from "three/examples/jsm/loaders/FontLoader"
import { SEGMENT_WIDTH, SEGMENT_HEIGHT, SEGMENT_DEPTH } from "./const"
import { Host } from "./Host"
import { InteractiveMap } from "./InteractiveMap"

const textureLoader = new TextureLoader()
const fontLoader = new FontLoader()
export type Connection = {
    from: any
    to: any,
    lines?: any
}

export class Segment {
    #id: string
    #hosts: Host[] = []
    // @ts-ignore
    #textInstance: Mesh<TextGeometry, MeshLambertMaterial>
    #instance: Group
    #name: string
    #interactiveMap: InteractiveMap
    connections: Connection[] = []

    constructor(text: string, x: number, z: number, interactiveMap: InteractiveMap) {
        this.#id = text
        this.#name = text
        this.#instance = this.create(text, x, z)
        this.#interactiveMap = interactiveMap
        this.createText(text)
    }

    create(id: string, x: number, z: number): Group {
        const texture = textureLoader.load(segmentTexture)
        const segmentMaterial = new MeshStandardMaterial({
            name: `segment_${id}`,
            map: texture,
        })
        const segment = new BoxGeometry(SEGMENT_WIDTH, SEGMENT_HEIGHT, SEGMENT_DEPTH)
        const segmentMesh = new Mesh(segment, segmentMaterial)
        segmentMesh.rotation.x = -1.57
        segmentMesh.castShadow = true
        segmentMesh.name = "mainMesh"
        const segmentGroup = new Group()

        segmentGroup.add(segmentMesh)
        segmentGroup.name = `segmentGroup_${id}`

        segmentGroup.position.set(x, SEGMENT_DEPTH / 2, z)
        segmentGroup.userData = {
            insatnce: this,
            connections: {from: [], to: []}
        }
        return segmentGroup
    }

    createText(text: string): void {
        fontLoader.load(
            "https://cdn.rawgit.com/mrdoob/three.js/master/examples/fonts/helvetiker_regular.typeface.json",
            (font) => {
                const geometry = new TextGeometry(text, {
                    font: font,
                    size: 40,
                    height: 4,
                    curveSegments: 1,
                    bevelEnabled: true,
                    bevelThickness: 1,
                    bevelSize: 1,
                    bevelSegments: 5,
                })

                let material = new MeshLambertMaterial({
                    color: 0xf3ffe2,
                    name: "text",
                    side: DoubleSide,
                })

                const textMesh = new Mesh(geometry, material)
                textMesh.position.set(-1.8, 0, 1.8)
                textMesh.rotation.x = -Math.PI / 2
                textMesh.scale.multiplyScalar(0.01)

                this.#instance.add(textMesh)
                this.#textInstance = textMesh
            }
        )
    }

    createHost(text: string, ip: string, texture: Texture | undefined): Host {
        const perLine = 3 // 0 1 2    3 4 5
        const stepX = 1.5
        const startX = -1.5

        const startZ = -1.5
        const stepZ = 1

        const lines = Math.floor(this.#hosts.length / perLine)
        const x = startX + (this.#hosts.length % perLine) * stepX
        const z = startZ + lines * stepZ
        const host = new Host(this, x, z, text, ip, texture)
        this.#instance.add(host.instance)
        this.#hosts.push(host)
        return host
    }

    get instance(): Group {
        return this.#instance
    }

    get name(): string {
        return this.#name
    }

    get interactiveMap(): InteractiveMap {
        return this.#interactiveMap
    }

    get hosts(): Host[] {
        return this.#hosts
    }
}
