import { Component, Vue } from "vue-property-decorator"
// @ts-ignore
import hostImage from "../../features/InteractiveMap/textures/host.jpg"
const textureLoader = new TextureLoader()
import { TextureLoader, Color, Vector3, BoxGeometry, MeshStandardMaterial, Mesh, Object3D, Event } from "three"
// @ts-ignore
import { InteractiveMap, EdgeLine, Size } from "../../features/InteractiveMap"
// @ts-ignore
import mapData from "../../interactiveMap.json"

@Component
export default class TrainingInteractiveMap extends Vue {
    isStarted: boolean = false
    interactiveMap: InteractiveMap | undefined
    attackSegmentLines: Mesh<BoxGeometry, MeshStandardMaterial>[] = []
    attackHostLines: Mesh<BoxGeometry, MeshStandardMaterial>[] = []
    segmentFrom: number = 0
    segmentTo: number | undefined = 1
    segments: string[] = ["Internet", "DMZ", "Data Center", "Office users", "ASU"]

    mounted() {
        this.isStarted = true
        this.start(this.canvas, this.hostHUD)
    }

    start(canvas: HTMLCanvasElement, hostHUD: HTMLElement): InteractiveMap {
        const rendererSizes: Size = {
            width: window.innerWidth - 20,
            height: window.innerHeight - 20,
        }
        const lightPositions: Vector3 = new Vector3(10, 10, -4)
        const IM = new InteractiveMap(rendererSizes, lightPositions, canvas, hostHUD)
        this.interactiveMap = IM

        const segments = []
        const routers = []

        textureLoader.load(hostImage, (texture) => {
            texture.repeat.x = 0.4
            texture.repeat.y = 0.4
            texture.offset.set(0.5, 0.5)

            mapData.segments.forEach((segment: any) => {
                const newSegment = IM.createSegment(segment.name)
                segments.push(newSegment)
                segment.hosts?.forEach((host: any) => {
                    newSegment.createHost(host.name, host.ip, texture)
                })
            })

            mapData.routers.forEach((router: any) => {
                if (!router.segments) return
                const segmentFrom = IM.scene.getObjectByName(`segmentGroup_${router.segments.from}`)

                const fromX = segmentFrom?.position.x || 0
                const fromZ = segmentFrom?.position.z || 0

                const stepX = 3
                const stepZ = 5
                const x = fromX + stepX
                const z = fromZ + stepZ
                const newRouter = IM.createRouter(router.name, x, z)
                routers.push(newRouter)

                router.segments.to.forEach((segment: any) => {
                    const segmentTo = IM.scene.getObjectByName(`segmentGroup_${segment}`)
                    // @ts-ignore
                    IM.connectRouterTo(newRouter, segmentTo)
                })

                // @ts-ignore
                IM.connectRouterTo(newRouter, segmentFrom)
            })
            const segmentFrom = IM.scene.getObjectByName(`segmentGroup_Internet`)
            const segmentTo = IM.scene.getObjectByName(`segmentGroup_ASU`)
            // if (segmentFrom && segmentTo) IM.attack(segmentFrom, segmentTo)
        })

        return IM
    }

    startAttackSegment(): void {
        if (!this.interactiveMap || this.segmentTo === undefined || this.segmentFrom === undefined) return

        this.resetAttack()
        // return
        // @ts-ignore
        switch (this.segments[this.segmentFrom]) {
            case "Internet":
                switch (this.segments[this.segmentTo]) {
                    case "DMZ":
                        this.interactiveMap?.fromInternetToRouter().then(() => this.interactiveMap?.fromRouterToDMZ())
                        break
                    case "Data Center":
                        this.interactiveMap
                            ?.fromInternetToRouter()
                            .then(() => this.interactiveMap?.fromRouterToDMZ())
                            .then(() => this.interactiveMap?.fromDMZToRouter())
                            .then(() => this.interactiveMap?.fromRouterToDataCenter())
                        break
                    case "Office users":
                        this.interactiveMap
                            ?.fromInternetToRouter()
                            .then(() => this.interactiveMap?.fromRouterToDMZ())
                            .then(() => this.interactiveMap?.fromDMZToRouter())
                            .then(() => this.interactiveMap?.fromRouterToOfficeUsers())
                        break
                    case "ASU":
                        this.interactiveMap
                            ?.fromInternetToRouter()
                            .then(() => this.interactiveMap?.fromRouterToDMZ())
                            .then(() => this.interactiveMap?.fromDMZToRouter())
                            .then(() => this.interactiveMap?.fromRouterToASU())
                        break
                }
                break
            case "DMZ":
                switch (this.segments[this.segmentTo]) {
                    case "Data Center":
                        this.interactiveMap?.fromDMZToRouter().then(() => this.interactiveMap?.fromRouterToDataCenter())
                        break
                    case "Office users":
                        this.interactiveMap
                            ?.fromDMZToRouter()
                            .then(() => this.interactiveMap?.fromDMZToRouter())
                            .then(() => this.interactiveMap?.fromRouterToOfficeUsers())
                        break
                    case "ASU":
                        this.interactiveMap
                            ?.fromDMZToRouter()
                            .then(() => this.interactiveMap?.fromDMZToRouter())
                            .then(() => this.interactiveMap?.fromRouterToASU())
                        break
                }
                break
            case "Data Center":
                switch (this.segments[this.segmentTo]) {
                    case "Office users":
                        this.interactiveMap.fromDataCenterToRouter()
                            .then(() => this.interactiveMap?.fromRouterToOfficeUsers())
                        break
                    case "ASU":
                        this.interactiveMap.fromDataCenterToRouter()
                            .then(() => this.interactiveMap?.fromRouterToASU())
                        break
                }
                break
            case "Office users":
                switch (this.segments[this.segmentTo]) {
                    case "ASU":
                        this.interactiveMap
                            ?.fromOfficeUserToRouter()
                            .then(() => this.interactiveMap?.fromRouterToASU())
                        break
                }
                break
        }
    }

    startAttackHost(): void {
        this.resetAttack()
        // @ts-ignore
        const host = this.interactiveMap.scene
            .getObjectByName("hostGroup_Administrator Workstation")
            .getObjectByName("host")
        // @ts-ignore
        host.material.color = new Color("transparent")
        this.interactiveMap
            ?.fromInternetToRouter()
            .then(() => this.interactiveMap?.fromRouterToDMZ())
            .then(() => this.interactiveMap?.fromDMZToRouter())
            .then(() => this.interactiveMap?.fromRouterToOfficeUsers())
            .then(
                () =>
                    // @ts-ignore
                    (host.material.color = new Color("red"))
            )
    }

    resetAttack() {
        this.interactiveMap?.resetAttack()
    }

    addAttackAnimation(
        animationName: string,
        linesData: EdgeLine[],
        attackLines: Mesh<BoxGeometry, MeshStandardMaterial>[],
        target: Object3D<Event>
    ) {
        if (!this.interactiveMap) return

        let animateStep = 0
        this.interactiveMap.elementsForAnimated[animationName] = () => {
            if (animateStep >= attackLines.length) {
                delete this.interactiveMap?.elementsForAnimated[animationName]
                // @ts-ignore
                target.material.color = new Color("red")
                return
            }
            const lineData = linesData[animateStep]?.animation
            if (!lineData) return

            const line: any = attackLines[animateStep]
            if (!line) return

            if (!line.material.visible) line.material.visible = true

            const curScale = line.scale[lineData.direction]
            if (curScale < lineData.toScale) {
                line.scale[lineData.direction] += 0.1
            }

            const curPosition = line.position[lineData.direction]

            if (lineData.method == "+" && curPosition < lineData.toPosition) {
                line.position[lineData.direction] += 0.05
            } else if (lineData.method == "-" && curPosition > lineData.toPosition) {
                line.position[lineData.direction] -= 0.05
            } else {
                animateStep++
            }
        }
    }

    isDisabled(segmentIndex: number): boolean {
        return segmentIndex <= this.segmentFrom
    }

    checkSegmentTo() {
        if (this.segmentTo === undefined) return
        if (this.segmentTo <= this.segmentFrom) this.segmentTo = undefined
    }

    get canvas(): HTMLCanvasElement {
        return this.$refs.canvas as HTMLCanvasElement
    }
    get hostHUD(): HTMLElement {
        return this.$refs.hostHUD as HTMLElement
    }
}
