    fromRouterToDataCenter() {
        return new Promise((res, rej) => {
            const nameFrom = "routerGroup_Router 2"
            const nameTo = "segmentGroup_Data Center"
            const animName = "fromRouterToDataCenter"
            let direction = "x"
            const isReverse = false

            const from = this.scene.getObjectByName(nameFrom)
            const to = this.scene.getObjectByName(nameTo)
            const connection = this.connections.find((el: any) => el.from.name === nameFrom && el.to.name === nameTo)
            const lines = connection?.lines

            let linesToDraw = lines.map((line: any) => {
                return {
                    ...line.data,
                    y: 0.1,
                    color: "red",
                    animation: true,
                }
            })
            if (isReverse) linesToDraw.reverse()
            let attackLines = this.createLine(linesToDraw)
            this.attackLines.push(...attackLines)
            let line = attackLines[0]
            let mesh = line.mesh
            mesh.scale[direction] = 0
            mesh.position[direction] -= line.data.size / 2

            this.elementsForAnimated[`${animName}_1`] = () => {
                if (!mesh.material.visible) mesh.material.visible = true

                if (mesh.scale[direction] < 1) {
                    mesh.scale[direction] += 0.01
                    mesh.position[direction] += line.data.size / 200
                } else {
                    this.elementsForAnimated[`${animName}_2`] = () => {
                        const lines = connection?.lines
                        let linesToDraw2 = lines.map((line: any) => {
                            return {
                                ...line.data,
                                y: 0.1,
                                color: "red",
                                animation: true,
                            }
                        })
                        let attackLines2 = this.createLine(linesToDraw2)
                        this.attackLines.push(...attackLines2)
                        let line2 = attackLines2[1]
                        let mesh2 = line2.mesh
                        if (!mesh2.material.visible) mesh2.material.visible = true

                        if (mesh2.scale[direction] < 1) {
                            mesh2.scale[direction] += 0.01
                            mesh2.position[direction] += line2.data.size / 200
                        }

                        let linesToDraw = lines.map((line: any) => {
                            return {
                                ...line.data,
                                y: 0.1,
                                color: "red",
                                animation: true,
                            }
                        })
                        if (isReverse) linesToDraw.reverse()
                        let attackLines = this.createLine(linesToDraw)
                        this.attackLines.push(...attackLines)
                        let line = attackLines[0]
                        let mesh = line.mesh
                        mesh.scale[direction] = 0
                        mesh.position[direction] -= line.data.size / 2
                        // @ts-ignore
                        to.getObjectByName("mainMesh").material.color = new Color("red")
                        delete this.elementsForAnimated[`${animName}_1`]
                        delete this.elementsForAnimated[`${animName}_2`]
                        res("")
                    }
                }
            }
        })
    }

